import React, {
  useCallback, useEffect, useMemo, useState
} from 'react';
import { useTranslation } from 'react-i18next';
import {
  Box, Button, FormControl, FormLabel, InputRightElement, Input, useStyleConfig, InputGroup, Select, Text
} from '@chakra-ui/react';
import { shallowEqual } from 'react-redux';

import { useNavigate } from 'react-router-dom';
import MiniBox from '../../../../layout/MiniBox';
import style from './SellAmount.module.css';
import { sendMessage } from '../../../../utils/sdk';
import { useAppAsyncDispatch, useAppSelector } from '../../../../hooks/store';
import { listAllAccounts } from '../../../../store/middlewares/account';
import { getMinLimit, getPrecision } from '../../../../utils/crypto';
import { getSellQuote, getSupportedCurrencies } from '../../../../store/middlewares/crypto';
import { apiErrorHandler } from '../../../../utils/general';
import { updateSellFormData } from '../../../../store/actionCreators/crypto';
import { useModulePageData } from '../../../../hooks/module';
import { SellCryptoData } from '../../../../modules/Crypto/SellCrypto';
import { Data } from '../../../../modules/Crypto';
import { COMMON } from '../../../../const/messages';
import CurrencyInput from '../../../../components/CurrencyInput';

const SellAmount = () => {
  const { t } = useTranslation();
  const dispatch = useAppAsyncDispatch();
  const navigate = useNavigate();

  const formControlStyle:any = useStyleConfig('FormControl', {
    variant: 'primary'
  });

  const { accounts, selectedAccount } = useAppSelector((store) => store.account, shallowEqual);
  const { selectedAccount: cryptoAccount, supportedCurrencies } = useAppSelector((store) => store.crypto, shallowEqual);

  const { showBack } = useModulePageData<SellCryptoData, Data<'sellCrypto'>['startScreen']>('sellCrypto');

  const [accountId, updateAccountId] = useState(selectedAccount?.id || '');
  const [amount, updateAmount] = useState('');
  const [amountType, updateAmountType] = useState('quantity');
  const [description, updateDescription] = useState('');

  const allAccounts = useMemo(() => accounts.filter((a) => a.type !== 'cardAccount'), [accounts]);

  const amountPrecision = useMemo(() => {
    if (amountType === 'quantity' && cryptoAccount?.currency) {
      return getPrecision(cryptoAccount?.currency, 'sell');
    }
    return 2;
  }, [amountType, cryptoAccount, supportedCurrencies]);

  const min = useMemo(() => {
    if (cryptoAccount?.currency) {
      return getMinLimit(cryptoAccount?.currency, 'sell', amountType);
    }
    return 0;
  }, [amountType, cryptoAccount, supportedCurrencies]);

  const defaultValue = useMemo(() => `0.${'0'.repeat(amountPrecision)}`, [amountPrecision]);

  const isValid = useMemo(() => amount !== '' && parseFloat(amount) > 0 && amountType !== '' && accountId !== '' && description !== '', [amount, amountType, accountId, description]);

  // useEffect(() => {
  //   updateAmount(defaultValue);
  // }, [amountPrecision, defaultValue]);

  useEffect(() => {
    dispatch(getSupportedCurrencies());
  }, []);

  useEffect(() => {
    dispatch(listAllAccounts(null, undefined, 50));
  }, []);

  const onSubmit = useCallback(() => {
    if (isValid && cryptoAccount) {
      dispatch(getSellQuote({
        walletId: cryptoAccount.id,
        cryptoCurrency: cryptoAccount.currency,
        amount: amountType === 'amount' ? amount : undefined,
        quantity: amountType === 'quantity' ? amount : undefined,
      })).then(() => {
        dispatch(updateSellFormData({
          selectedAccount: accountId,
          selectedAccountLabel: allAccounts.find((a) => a.id === accountId)?.label || '',
          amount,
          type: amountType,
          description
        }));
        navigate('/crypto/sell/preview');
      }).catch(apiErrorHandler);
    }
  }, [isValid, amount, amountType, accountId, description, cryptoAccount?.currency]);

  return (
    <div className="page">
      <form
        autoComplete="off"
        onSubmit={(e) => {
          e.preventDefault();
          onSubmit();
        }}
      >
        <MiniBox
          heading={t('crypto_sell_account.title')}
          showBack={showBack}
          footer={(
            <Button disabled={!isValid} width="100%" variant="primary" type="submit">
              {t('crypto_sell_account.ctaText')}
            </Button>
          )}
          onBack={() => {
            sendMessage(COMMON.BACK_PRESSED, {
              from: 'sellCrypto'
            });
          }}
        >
          <Box width="100%" paddingTop="15px">
            <FormControl variant="primary" style={formControlStyle}>
              <FormLabel variant="primary">
                {t('crypto_sell_account.fundingAccount')}
              </FormLabel>
              <Select placeholder={t('crypto_sell_account.select')} variant="primary" value={accountId} onChange={(e) => updateAccountId(e.target.value)}>
                {allAccounts.map((a) => <option key={`account-${a.id}`} value={a.id}>{a.label}</option>)}
              </Select>
            </FormControl>
            <FormControl variant="primary" style={formControlStyle}>
              <FormLabel variant="primary">
                {t('crypto_sell_account.amountQuantity')}
              </FormLabel>
              <InputGroup className={style.currencyInput}>
                <CurrencyInput
                  prefix=""
                  digits={amountPrecision}
                  placeholder={defaultValue}
                  value={amount}
                  onChange={(value) => {
                    updateAmount(value);
                  }}
                />
                <InputRightElement
                  className={style.inputRightElement}
                  // eslint-disable-next-line react/no-children-prop
                  children={(
                    <Select boxShadow="none !important" outline="none" fontSize="14px" border="0" value={amountType} onChange={(e) => updateAmountType(e.target.value)}>
                      <option value="quantity">
                        {cryptoAccount?.currency}
                      </option>
                      <option value="amount">USD</option>
                    </Select>
                )}
                />
              </InputGroup>
              <Text variant="small" className="text-light" textAlign="right">
                {`Min: ${min} ${amountType === 'quantity' ? cryptoAccount?.currency : 'USD'}`}
              </Text>
            </FormControl>

            <FormControl variant="primary" style={formControlStyle}>
              <FormLabel variant="primary">
                {t('crypto_sell_account.desc')}
              </FormLabel>
              <Input
                value={description}
                onChange={(e) => updateDescription(e.target.value)}
                placeholder={t('crypto_sell_account.desc')}
                variant="primary"
              />
            </FormControl>
          </Box>
        </MiniBox>
      </form>

    </div>
  );
};

export default SellAmount;
