import store from '../store';

/**
 * Get min limit
 * @param currency
 * @param source
 * @param amountType
 * @returns {number}
 */
export const getMinLimit = (currency: string, source: 'send' | 'buy' | 'sell', amountType: string = 'quantity') => {
  const supportedCurrencies = store.getState().crypto.supportedCurrencies.data;

  const selectedCurrency = supportedCurrencies.find((c) => c.currency === currency);

  if (selectedCurrency) {
    const limit = selectedCurrency.limits[source];
    return amountType === 'quantity' ? limit.minimumQty : limit.minimumAmount || '0';
  }
  return '0';
};

export const getPrecision = (currency: string, source: 'send' | 'buy' | 'sell') => {
  const supportedCurrencies = store.getState().crypto.supportedCurrencies.data;

  const selectedCurrency = supportedCurrencies.find((c) => c.currency === currency);

  if (selectedCurrency) {
    const limit = selectedCurrency.limits[source];
    const precision = limit.minimumQty?.split('.')[1]?.length || 2;
    return precision !== 0 ? precision : 2;
  }
  return 6;
};
